@import "../../variables";

.footer {
  padding: 1em 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  font-size: 0.8rem;

  background: rgb(250, 250, 250);
  background: linear-gradient(
    180deg,
    rgba(250, 250, 250, 1) 0%,
    rgba(224, 251, 252, 1) 100%
  );

  &__text {
    color: $color-gray;
  }

  &__link {
    color: $color-gray;

    &:hover {
      color: $color-accent;
    }
  }
}
