@import "../../variables";

.header {
  width: 100%;
  padding: 0 5%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  background: rgb(224, 251, 252);
  background: linear-gradient(
    180deg,
    rgba(224, 251, 252, 1) 0%,
    rgba(250, 250, 250, 1) 100%
  );

  &__title {
    font-size: 3rem;
    margin: 1rem 0;
    color: $color-accent;
  }

  &__text {
    font-size: 2rem;
  }
}
